import React, { Component } from 'react';
import { Row, Col, Form, InputNumber } from 'antd';
import { BtnSearch, BtnClear } from 'components/common/button';
import YearSemesterInput from 'components/common/input/YearSemesterInput';
import { SelectAjax } from 'components/common';
import { getTrainTypes } from 'services/trainType';
import { getFacultys } from 'services/faculty';
import { getListSubject } from 'services/Subject';
import { getTestRoomForSelect } from 'services/testOrganizing';

@Form.create({ name: 'formCadenceSearchForm1' })
class SearchBar extends Component {
  state = {
    testRoomData: []
  };

  getTestRoomForSelect = async (params = {}) => {
    const res = await getTestRoomForSelect(params);
    this.setState({ testRoomData: (res.data && res.data.items) || [] });
    return res;
  }

  handleReset = () => {
    this.props.form.resetFields();
    this.props.handleClearForm()
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      const { testRoomData } = this.state;
      let selectedToId = null;
      if (values.tOrId) {
        const selectedObject = testRoomData.find(x => x.value === values.tOrId) || {};
        const { toId } = selectedObject;
        selectedToId = toId;
      }
      this.props.handleSearch({ ...values, toId: selectedToId });
    });
  };

  render () {
    const { getFieldDecorator, setFieldsValue, getFieldValue } = this.props.form;
    const trainTypeId = getFieldValue('trainTypeId');
    const facultyId = getFieldValue('facultyId');
    const yearEduId = getFieldValue('yearEduId');
    const semesterId = getFieldValue('semesterId');
    const subjectId = getFieldValue('subjectId');
    const examBatch = getFieldValue('examBatch');
    const optionFieldDecorator = {
      trainTypeId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Hệ!',
          },
        ],
      },
      facultyId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Khoa!',
          },
        ],
      },
      subjectId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn học phần',
          },
        ],
      },
      studyFieldId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn CN',
          },
        ],
      },
      yearEduId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Năm học',
          },
        ],
      },
      semesterId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Học kỳ',
          },
        ],
      },
    };
    return (
      <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <Col xs={24} sm={12} md={6}>
            <Form.Item label="Hệ">
              {getFieldDecorator('trainTypeId', optionFieldDecorator.trainTypeId)(<SelectAjax service={getTrainTypes} />)}
            </Form.Item>
          </Col>
          <Col xs={24} sm={12} md={6}>
            <Form.Item label="Khoa">
              {getFieldDecorator('facultyId', optionFieldDecorator.facultyId)(
                <SelectAjax
                  setFieldsValue={setFieldsValue}
                  service={getFacultys}
                  params={{ trainTypeId }}
                  allowGetData={!!trainTypeId}
                />
              )}
            </Form.Item>
          </Col>
          <YearSemesterInput form={this.props.form} optionFieldDecorator={optionFieldDecorator} />
        </Row>
        <Row gutter={24}>
          <Col xs={24} sm={12} md={6}>
            <Form.Item label="Học phần">
              {getFieldDecorator('subjectId', optionFieldDecorator.subjectId)(
                <SelectAjax
                  service={getListSubject}
                  setFieldsValue={setFieldsValue}
                  params={{ trainTypeId, facultyId, yearEduId, semesterId }}
                  allowGetData={!!trainTypeId && !!facultyId && !!yearEduId && !!semesterId}
                />
              )}
            </Form.Item>
          </Col>
          <Col xs={24} sm={12} md={6}>
            <Form.Item label="Đợt thi">
              {getFieldDecorator('examBatch', {
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng chọn đợt thi!',
                  },
                ],
              })(
                <InputNumber />
              )}
            </Form.Item>
          </Col>
          <Col xs={24} sm={12} md={6}>
            <Form.Item label="Phòng thi">
              {getFieldDecorator('tOrId', {})(
                <SelectAjax
                  service={this.getTestRoomForSelect}
                  setFieldsValue={setFieldsValue}
                  params={{ trainTypeId, facultyId, yearEduId, semesterId, subjectId, examBatch }}
                  allowGetData={!!trainTypeId && !!facultyId && !!yearEduId && !!semesterId && !!subjectId && !!examBatch}
                  keyValue="value"
                // onChange={this.test}
                />
              )}
            </Form.Item>
          </Col>
          <Col xs={24} sm={12} md={6}>
            <div className="btn-group">
              <BtnSearch onClick={this.handleSearch} />
              <BtnClear onClick={this.handleReset} />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default SearchBar;



