import React, { Component, Fragment } from 'react';
import { Card, Row, Table, Col, message, Button, Popconfirm } from 'antd';
import Page from 'components/LayoutComponents/Page';
import { SelectAjax } from 'components/common';
import { getStudentInTO } from 'services/testOrganizing';
import { getStudentInTestPackage, createDetachHeadNoAuto, deleteTestPackage, getTestPackages } from 'services/testPackage';
import SearchBar from './SearchBar';
import SearchBar1 from './SearchBar1';

const column1 = [
  {
    title: 'Số báo danh',
    dataIndex: 'testCode',
    key: 'testCode',
  },
  {
    title: 'Mã sinh viên',
    dataIndex: 'studentCode',
    key: 'studentCode',
  },
  {
    title: 'Họ và tên',
    dataIndex: 'fullname',
    key: 'fullname',
  },
  {
    title: 'Ngày sinh',
    dataIndex: 'dob',
    key: 'dob',
  },
  {
    title: 'Lớp',
    dataIndex: 'classname',
    key: 'classname',
  },
  {
    title: 'Phòng thi',
    dataIndex: 'room',
    key: 'room',
  },
];
const column2 = [
  {
    title: 'Số báo danh',
    dataIndex: 'testCode',
    key: 'testCode',
  },
  {
    title: 'Số phách',
    dataIndex: 'detachHeadNo',
    key: 'detachHeadNo',
  },
  {
    title: 'Họ và tên',
    dataIndex: 'fullname',
    key: 'fullname',
  },
  {
    title: 'Ngày sinh',
    dataIndex: 'dob',
    key: 'dob',
  },
  {
    title: 'Lớp',
    dataIndex: 'classname',
    key: 'classname',
  },
  {
    title: 'Phòng thi',
    dataIndex: 'room',
    key: 'room',
  },
];

class CadenceCustomPage extends Component {
  state = {
    tableColumns: column1,
    tableData: [],
    // tableData2: [],
    searchStudentFormValues: {},
    isSelectedPackage: false,
    packageNo: null,
    toId: null,
    tOrId: null,
    isSearchStudentInTO: false,
  };

  handlecreateDetachHeadNoAuto = async (formValues) => {
    try {
      const { toId, tOrId, packageNo } = this.state;
      this.setState({ isCreatingTestPackage: true });
      const resquestData = {
        ...formValues,
        toId,
        tOrId
      };

      await createDetachHeadNoAuto(resquestData);
      if (packageNo) {
        this.handleChangePackage(packageNo);
      }
      message.success('Lập số phách thành công');
    } catch (err) {
      console.log(err);
      message.success('Có lỗi xãy ra!');
    } finally {
      this.setState({ isCreatingTestPackage: false })
    }
  }

  handleDeleteTestPackage = async () => {
    try {
      const { toId, packageNo } = this.state;
      if (toId && packageNo) {
        await deleteTestPackage({ toId, packageNo });
        this.handleChangePackage(packageNo);
        message.success('Xoá thành công');
      } else {
        message.warn('Vui lòng chọn phòng thi và túi thi!');
      }
    } catch (err) {
      message.error('Có lỗi xãy ra');
    }
  }

  handleSearch = async (formValues) => {
    try {
      this.setState({ isSearchStudentInTO: true, searchStudentFormValues: formValues, toId: formValues.toId, tOrId: formValues.tOrId, packageNo: null });

      const res = await getStudentInTO({ tOId: formValues.toId, tOrId: formValues.tOrId });
      if (res.data && res.data.items) {
        this.setState({ tableData: res.data.items, tableColumns: column1 });
      }
    } catch (err) {
      message.error('Có lỗi xãy ra!');
    } finally {
      this.setState({ isSearchStudentInTO: false });
    }
  }

  handleChangePackage = async (packageNo) => {
    if (Object.keys(this.state.searchStudentFormValues).length === 0) {
      this.setState({ tableData: [], tableColumns: column1 });
      return;
    }

    if (packageNo) {
      try {
        const { toId, tOrId } = this.state;
        this.setState({ isSearchStudentInTO: true, isSelectedPackage: true, packageNo });

        const res = await getStudentInTestPackage({ tOId: toId, tOrId, packageNo });
        if (res.data && res.data.items) {
          this.setState({ tableData: res.data.items, tableColumns: column2 });
        }
      } catch (err) {
        message.error('Có lỗi xãy ra!');
      } finally {
        this.setState({ isSearchStudentInTO: false });
      }
    }
    else {
      this.handleSearch(this.state.searchStudentFormValues)
    }
  }

  handleClearForm = () => {
    this.setState({
      tableColumns: column1,
      tableData: [],
      searchStudentFormValues: {},
      isSelectedPackage: false,
      packageNo: null,
      toId: null,
      tOrId: null,
      isSearchStudentInTO: false
    });
  }

  render () {
    const {
      tableColumns,
      tableData,
      isSearchStudentInTO,
      isCreatingTestPackage,
      isSelectedPackage,
      toId,
      packageNo
    } = this.state;

    return (
      <Fragment>
        <Card title="Đánh phách">
          <Row>
            <Col span={24}>
              <SearchBar
                handleSearch={this.handleSearch}
                handleClearForm={this.handleClearForm}
              />
            </Col>
          </Row>
          <Row>
            <Col span={24} >
              <p>&nbsp;</p>
            </Col>
          </Row>
          <Row>
            <Col span={24}>
              <SearchBar1
                isSelectedPackage={isSelectedPackage}
                handleCreateDetachHeadNoAuto={this.handlecreateDetachHeadNoAuto}
                isCreatingTestPackage={isCreatingTestPackage}
                toId={toId}
              />
            </Col>
          </Row>
          <Row>
            <Col span={24}>
              <div className="btn-group" style={{ float: 'right' }}>
                <SelectAjax
                  service={getTestPackages}
                  onChange={this.handleChangePackage}
                  keyValue="value"
                  disabled={!toId}
                  value={!packageNo ? [] : [packageNo]}
                />
                <Popconfirm
                  title="Bạn có muốn xóa?"
                  okText="Xóa"
                  cancelText="Hủy"
                  onConfirm={() => this.handleDeleteTestPackage()}
                  disabled={!toId || !packageNo}
                >
                  <Button type="primary" disabled={!toId || !packageNo} loading={isCreatingTestPackage} >Xóa túi thi</Button>
                </Popconfirm>
              </div>
            </Col>
          </Row>
          <Row>
            <Col span={24}>
              <Table
                bordered
                dataSource={tableData}
                columns={tableColumns}
                loading={isSearchStudentInTO}
              />
            </Col>
          </Row>
        </Card>
      </Fragment>
    );
  }
}

export default props => (
  <Page {...props}>
    <CadenceCustomPage />
  </Page>
);



