import React, { Component } from 'react';
import { Row, Col, Form, Input, Button, Checkbox } from 'antd';

@Form.create({ name: 'formCadenceSearchForm2' })
class SearchBar1 extends Component {
  state = {};

  handleAdd = () => {
    this.props.handleAdd();
  };

  handleCreateDetachHeadNoAuto = () => {
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      this.props.handleCreateDetachHeadNoAuto(values);
    });
  };

  handleDelete = () => {
    this.props.handleDelete();
  };

  handleDeleteTestPackage = () => {
    this.props.handleDeleteTestPackage();
  };

  handleReset = () => {
    this.props.form.resetFields();
  };

  // handleSelectPackage = packageNo => {
  //   this.props.handleChangePackage(packageNo);
  // }

  render() {
    const { isCreatingTestPackage, toId } = this.props;
    const { getFieldDecorator } = this.props.form;
    return (
      <Form className="form-inline-label" layout="vertical">
        <Row gutter={24}>
          <Col xs={24} sm={12} md={12} lg={6}>
            <Form.Item label="Ký hiệu phách">
              {getFieldDecorator('headSignal', {
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng chọn ký hiệu phách!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
          </Col>
          <Col xs={24} sm={12} md={12} lg={6}>
            <Form.Item label="Số phách từ">
              {getFieldDecorator('fromNo', {
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng chọn số phách!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
          </Col>

          <Col xs={24} sm={12} md={12} lg={6}>
            <Form.Item label="Cắt số Sinh SV/DS">
              {getFieldDecorator('cutNoFromList', {
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng chọn số sinh viên!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
          </Col>
          <Col xs={24} sm={12} md={12} lg={6}>
            <Form.Item label="Cắt số Sinh SV/Phòng">
              {getFieldDecorator('cutNoFromRoom', {
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng chọn số sinh viên!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
          </Col>
        </Row>
        <Row gutter={24}>
          <Col xs={24} sm={12} md={12} lg={6}>
            <Form.Item label="Số sinh viên tối đa/túi">
              {getFieldDecorator('maxNoInTP', {
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng chọn số sinh viên!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
          </Col>
          <Col xs={24} sm={12} md={6} lg={6}>
            <Form.Item className="form-item-checkbox" label="Đánh phách trộn DS">
              {getFieldDecorator('assignDetachHeadNoFirst', {
                valuePropName: 'checked',
                initialValue: false,
              })(<Checkbox />)}
            </Form.Item>
          </Col>
          <Col xs={24} sm={12} md={6} lg={6}>
            <Form.Item className="form-item-checkbox" label="Cắt từ đầu DS">
              {getFieldDecorator('cutTop', {
                valuePropName: 'checked',
                initialValue: false,
              })(<Checkbox />)}
            </Form.Item>
          </Col>
          <Col xs={24} sm={3} md={3} lg={3}>
            <Button
              type="primary"
              onClick={this.handleCreateDetachHeadNoAuto}
              disabled={!toId}
              title={!toId ? 'Vui lòng chọn phòng thi và tìm kiếm' : ''}
              loading={isCreatingTestPackage}
            >
              Lập số phách
            </Button>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default SearchBar1;



